	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Shop Til You Drop","date":"30 Apr 2007","slug":"/2007-04-30-shop-til-you-drop/","tags":"Y2007"},"html":"<p>The regulation of shopping hours in Germany is now a matter for the states rather than the federal government, and most states have taken the opportunity to deregulate shopping hours Monday through Saturday. Here in Niedersachsen the deregulation took effect this month, and our local Rewe supermarket is now open until 9:00 pm 6 days a week. We've taken advantage of the extra hour a few times already. </p>\n<p>When we came to Germany the restricted shopping hours was the biggest thing I had to get used to. Back then shops closed at 6:30 pm during the week and 2:00 pm on Saturday. But the times have become longer and longer, first until 8:00 pm on weekdays, and then 8:00 pm every Saturday. Now it's a non-issue, and I imagine that sooner or later Sunday closing will also be a thing of the past.</p>"},"next":{"fields":{"title":"Lead the World","date":"30 Apr 2007","slug":"/2007-04-30-lead-the-world/"}},"previous":{"fields":{"title":"09-F9-11-02-M-O-U-S-E","date":"02 May 2007","slug":"/2007-05-02-09-f9-11-02-m-o-u-s-e/"}}},{"node":{"fields":{"title":"Lead the World","date":"30 Apr 2007","slug":"/2007-04-30-lead-the-world/","tags":"Y2007"},"html":"<p>Which American politician just said:</p>\n<blockquote><p>\n  America must \"lead the world in battling immediate evils and promoting the ultimate good.\"\n</p></blockquote>\n<p>Germans who haven't been following the US presidential race that closely <a href=\"http://www.washingtonpost.com/wp-dyn/content/article/2007/04/27/AR2007042702027.html\">might be surprised</a>. (via <a href=\"http://globalguerrillas.typepad.com/johnrobb/2007/04/\">John Robb</a>)</p>"},"next":{"fields":{"title":"McDonald's on Sabine Christiansen Tonight","date":"29 Apr 2007","slug":"/2007-04-29-mcdonalds-on-sabine-christiansen-tonight/"}},"previous":{"fields":{"title":"Shop Til You Drop","date":"30 Apr 2007","slug":"/2007-04-30-shop-til-you-drop/"}}},{"node":{"fields":{"title":"McDonald's on Sabine Christiansen Tonight","date":"29 Apr 2007","slug":"/2007-04-29-mcdonalds-on-sabine-christiansen-tonight/","tags":"Y2007"},"html":"<p>Tonight's <a href=\"http://www.sabinechristiansen.de/\">Sabine Christiansen</a> talk show on ARD is on the topic of obesity, and among the guests is <a href=\"http://www.mcdonalds.de/presse/basis/vorstand/goebel.html\">Wolfgang Goebel</a>, Vice President for Operations for McDonald's Deutschland (and, for a few more days, MamaMaus' boss).</p>\n<p>We're no big fans of Christiansen, but her show is the best known talk show on German television. We usually don't stay up until 21:45 on Sunday nights, but tonight we might make an exception. I do suspect, though, that the producers deliberately chose a soft topic for the holiday weekend.</p>"},"next":{"fields":{"title":"Back to Back","date":"29 Apr 2007","slug":"/2007-04-29-back-to-back/"}},"previous":{"fields":{"title":"Lead the World","date":"30 Apr 2007","slug":"/2007-04-30-lead-the-world/"}}}],"pathPrefix":"","first":false,"last":false,"index":134,"pageCount":1101}}

